.header {
    display: flex;
    justify-content: flex-end;
    padding: 10px;
    background-color: #cccccc;


    .header-links{
        display: flex;
        padding: 10px;
    }


    .nav-link-wrapper{
        height: 22px;
        border-bottom: 1px solid transparent;
        transition: 0.5s ease-in-out;

        margin-right: 20px;
        text-transform: uppercase;
        font-size: 2rem;

        a{
            color: #47a8bd;
            text-decoration: none;
            &:hover{
                color: #0a2463;
            }
        }

        &:hover{
            border-bottom: 1px solid #0a2463;
        }

        .nav-link-active {
            color: #47a8bd;
        }
    }
    
}

